<template>
  <div class="data">
    <div v-if="showToken">
      Token:<input v-model="token" @change="uploadToken" />
    </div>
    <div class="tools">
      <a @click="add()">添加+</a>
      <a style="float: right" @click="save()">保存</a>
    </div>
    <table border="0">
      <tr>
        <th>日期/身高</th>
        <th v-for="name in names" :key="name">{{ name }}</th>
        <th></th>
      </tr>
      <tr v-for="(row, i) in datas" :key="i">
        <td>
          <input v-model="row.date" />
        </td>
        <td v-for="(name, j) in names" :key="name + j">
          <input v-model="row.data[j]" />
        </td>
        <td><a @click="del(i, row)">删除</a></td>
      </tr>
    </table>
  </div>
</template>

<script>
import { upload2, getContent } from "@/common";
import storejs from "storejs";
export default {
  name: "Data",
  data() {
    return {
      token: "",
      names: ["Davie", "Jack", "Mumu"],
      datas: [],
      showToken: 0,
    };
  },
  mounted() {
    let self = this;
    getContent().then((resp) => {
      self.datas.length = 0;
      self.datas.push(...eval(resp));
      storejs.set("health", { names: self.names, datas: self.datas });
    });
  },
  methods: {
    uploadToken() {
      storejs.set("token", this.token);
    },
    add() {
      let d = new Date();
      this.datas.unshift({
        date: d.getFullYear() + "-" + ("0" + (d.getMonth() + 1)).substr(-2),
        data: [],
      });
    },
    del(i) {
      this.datas.splice(i, 1);
    },
    save() {
      let ok = this.datas.length > 0;
      for (let i = 0; i < this.datas.length; i++) {
        if (this.datas[i].data.filter((e) => e).length != this.names.length) {
          ok = 0;
          break;
        }
      }
      if (ok) {
        let str = JSON.stringify(this.datas);
        console.log(str);
        let str2 = JSON.stringify({ names: this.names, datas: this.datas });
        let health = JSON.stringify(storejs.get("health"));
        console.log(str2, health);
        if (str2 == health) {
          alert("no change");
          return;
        }

        let self = this;
        upload2(str)
          .then(() => {
            alert("upload ok");
            storejs.set("health", str2);
          })
          .catch((e) => {
            alert("upload error:" + e);
            self.showToken = 1;
          });
      } else {
        alert("还没填写完成");
      }
    },
  },
  computed: {},
  components: {},
};
</script>
<style scoped>
.tools {
  text-align: left;
  background: pink;
}
a {
  cursor: pointer;
  padding: 3px;
}
table tr:nth-child(even) {
  background-color: #ccc;
}
</style>
