import storejs from "storejs";
let name = "SMLOG";
let rep = "data";
let path = "health.json";

function utf8_to_b64(str) {
  return window.btoa(unescape(encodeURIComponent(str)));
}

function b64_to_utf8(str) {
  return decodeURIComponent(escape(window.atob(str)));
}

async function upload(name, rep, path, content) {
  let token = storejs.get("token");
  var auth = "token " + token;
  var url = `https://api.github.com/repos/${name}/${rep}/contents/${path}`;

  if (!token) throw "token?";
  let json = await fetch(url, {
    method: "get",
    headers: {
      accept: "application/vnd.github.v3+json",
    },
  }).then((r) => r.json());
  let sha = json.sha;
  json = await fetch(url, {
    method: "put",
    body: JSON.stringify({
      message: "update" + new Date(),
      branch: "main",
      content: utf8_to_b64(content),
      sha: sha,
    }),
    headers: {
      accept: "application/vnd.github.v3+json",
      Authorization: auth,
    },
  }).then((response) => {
    return response.json();
  });

  return json;
}
async function getBlobContent(name, rep, path, sha) {
  let token = storejs.get("token");
  var auth = "token " + token;

  let headers = {
    accept: "application/vnd.github.v3+json",
  };
  if (token) headers["Authorization"] = auth;

  let json = await fetch(
    `https://api.github.com/repos/${name}/${rep}/contents/${path}` +
      (sha ? `?ref=${sha}` : ""),
    {
      method: "get",
      headers: headers,
      cache: "force-cache",
    }
  ).then((r) => r.json());
  return b64_to_utf8(json.content);
}

export async function upload2(content) {
  return upload(name, rep, path, content);
}
export async function getContent(sha) {
  return getBlobContent(name, rep, path, sha);
}
